$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}Cart {
    &__container {
        display: flex;

        justify-content: space-between;
        padding: 0 112px;
    }

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        background-image: url('../../assets/images/map.png');

        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;

            background: linear-gradient(90.98deg, #001124 50.03%, rgba(0, 17, 36, 0) 99.56%);
        }
    }
}
