$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}Button {
    padding: 20px 40px;
    border-radius: 40px;

    display: flex;

    border: none;

    color: #fff;

    font-weight: 600;
    font-size: 32px;

    align-items: center;
    justify-content: center;

    background-color: rgba(255, 255, 255, .1);

    transition: background-color .3s ease-in-out;

    &:focus {
        background-color: #08A652;
    }
}
