$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}Status {
    &__container {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 206px 0 0;
    }

    &__order-number {
        font-size: 32px;
        font-weight: 600;
        line-height: 44px;
        color: rgba(255, 255, 255, 0.74);
    }

    &__order-time {
        margin-top: 16px;
        font-weight: bold;
        font-size: 64px;
        line-height: 72px;
        text-align: center;
        color: #fff;
    }

    &__success-button {
        margin-top: 60px;

        padding: 20px 40px;
        border-radius: 40px;

        display: flex;

        border: none;

        color: #fff;

        font-weight: 600;
        font-size: 32px;

        align-items: center;
        justify-content: center;

        background-color: rgba(255, 255, 255, .1);

        transition: background-color .3s ease-in-out;

        &:focus:not(&_disabled) {
            background-color: #08A652;
        }
    }

    &__figure-1 {
        position: absolute;
        top: 92px;
        left: 834px;

        height: 61px;
        width: 61px;

        background-image: url("./figure-1.svg");
    }

    &__figure-2 {
        position: absolute;
        top: 0;
        left: 482px;

        height: 61px;
        width: 61px;

        background-image: url("./figure-2.svg");
    }

    &__figure-3 {
        position: absolute;
        top: 280px;
        left: 236px;

        height: 46px;
        width: 46px;

        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-image: url("./figure-3.svg");
    }

    &__figure-4 {
        position: absolute;
        top: 158px;
        left: 1487px;

        height: 53px;
        width: 53px;

        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-image: url("./figure-4.svg");
    }

    &__figure-5 {
        position: absolute;
        top: 594px;
        left: 340px;

        height: 53px;
        width: 53px;

        transform: rotate(90deg);

        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-image: url("./figure-1.svg");
    }

    &__figure-6 {
        position: absolute;
        top: 500px;
        left: 652px;

        height: 60px;
        width: 60px;

        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-image: url("./figure-6.svg");
    }

    &__figure-7 {
        position: absolute;
        top: 705px;
        left: 1044px;

        height: 40px;
        width: 40px;

        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-image: url("./figure-7.svg");
    }

    &__figure-8 {
        position: absolute;
        top: 492px;
        left: 1268px;

        height: 40px;
        width: 40px;

        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-image: url("./figure-8.svg");
    }

    &__figure-9 {
        position: absolute;
        top: 590px;
        left: 1604px;

        height: 90px;
        width: 90px;

        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-image: url("./figure-9.svg");
    }
}
